import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
import uniTag from "@/components/uni-tag/uni-tag.vue"
import http from '@/components/utils/http.js'
import {
	formatDate
} from '../../components/date.js'
import {
	mapState
} from 'vuex'
export default {
	components: {
		uniTag,
		uniLoadMore
	},
	filters: {
		formatDate(time) {
			var date = new Date(time);
			return formatDate(date, 'yyyy-MM-dd')
		},
		getDateDiff (dateTimeStamp) {
		    var result = ''
		    var minute = 1000 * 60
		    var hour = minute * 60
		    var day = hour * 24
		    var month = day * 30
		    var now = new Date().getTime()
		    var diffValue = now - dateTimeStamp
		    if (diffValue < 0) return
		    var monthC = diffValue / month
		    var weekC = diffValue / (7 * day)
		    var dayC = diffValue / day
		    var hourC = diffValue / hour
		    var minC = diffValue / minute
		    if (monthC >= 1) {
		      result = "" + parseInt(monthC) + "月前"
		    }
		    else if (weekC>=1) {
		      result = "" + parseInt(weekC) + "周前"
		    }
		    else if (dayC >= 1) {
		      result = ""+ parseInt(dayC) + "天前"
		    }
		    else if (hourC >= 1){
		      result = "" + parseInt(hourC) + "小时前"
		    }
		    else if (minC >= 1) {
		      result = ""+ parseInt(minC) + "分钟前"
		    } else {
		      result = "刚刚"
		    }
		    return result
		}
	},
	computed: mapState(['token', 'user_id', 'group_id']),
	data() {
		return {
			itemList: [],
			keywordinput: '',
			keyword: '',
			infoType: 0,
			userid: 0,
			page: 1,
			nextPage: 1,
			status: 'more',//more   显示文字加载更多  loading  显示文字 正在加载   nomore  显示没有更多了
			groupid: false,
			lodingLock: false,
			contentText: {
				contentdown: '上拉显示更多',
				contentrefresh: '正在加载...',
				contentnomore: '———— 杭州市安庆商会 ————'
			},
		}
	},
	onPullDownRefresh() {
		console.log('refresh');
		var that = this
		that.itemList = []
		that.loadData()
		setTimeout(()=>{
			uni.stopPullDownRefresh();
		},1000);		
	},
	onLoad(e) {
		let that = this;
		let shareurl = e.shareurl;
		let share = e.share;
		if(share=="1"){
			let url=decodeURIComponent(shareurl);
			uni.navigateTo({
				// 传递 userid 在详情页接收
				url:url
			});
		}
		if (this.user_id && this.group_id == 2) {
			this.groupid = true
		}

		// 数据未加载前，可以放加载动画
		uni.showLoading({
			title: '加载中...',
		});

		this.loadData(() => {
			//加载完成的回调，隐藏加载框
			uni.hideLoading();
		});


		uni.$on("editInfo", () => {
			that.itemList = [];
			that.page= 1;
			that.nextPage= 1;
			that.status= 'more';
			that.loadData(() => {
				
			});
		});
	},
	onUnload(e) {
		uni.$off("editInfo", () => {
			//loadData();
		});
	},
	onReachBottom() {
		let that = this;
		if (that.nextPage > 0) {
			that.loadData(() => {

			});
		} else {

		}
	},
	onShareAppMessage(res) {
		if (res.from === 'button') { // 来自页面内分享按钮
			console.log(res.target)
		}
		return {
			title: '自定义分享标题',
			path: '/pages/gongqiu/gongqiu'
		}
	},
	methods: {
		//加载供求数据
		loadData(collback) {
			if (this.lodingLock) {
				return;
			}
			this.lodingLock = true;
			this.status = "loading"
			let that = this;
			let param = {
				page: that.nextPage,
				keyword: that.keyword,
				type: that.infoType,
			};
			http.apiRequest({
				api: "/api/info/infoList",
				data: param,
				secuss: (res, httpStatus) => {
					that.lodingLock = false;
					if (res.code == "1") {
						that.nextPage = res.data.nextPage;
						that.page = res.data.page;
						that.itemList = that.itemList.concat(res.data.rows);
						console.log(that.itemList)

						if (that.nextPage > 0) {
							that.status = "more";
						} else {
							that.status = 'nomore';
						}

						if (typeof(collback) == "function") {
							collback();
						}
					} else {
						if (that.nextPage > 0) {
							that.status = "more"
						} else {
							that.status = "nomore"
						}
						uni.showToast({
							title: res.msg,
							duration: 1500,
							icon: "none"
						});
						if (typeof(collback) == "function") {
							collback();
						}
					}
				},
				error: (res, httpStatus) => {
					//接口调用失败，可能是网络故障，可能是接口报错
					if (that.nextPage > 0) {
						that.status = "more"
					} else {
						that.status = "nomore"
					}
					that.lodingLock = false;
					uni.showToast({
						title: "接口调用失败，请检查网络",
						duration: 1500,
						icon: "none"
					});
					if (typeof(collback) == "function") {
						collback();
					}
				},
			});
		},
		
		//打开供求信息
		openinfo(e) {
			var itemId = e.currentTarget.dataset.itemid
			uni.navigateTo({
				// 传递 userid 在详情页接收
				url: './gongqiuDetail/gongqiuDetail?itemid=' + itemId
			});
		},
		//打开会员信息
		openmember(e) {
			var memberid = e.currentTarget.dataset.memberid
			uni.navigateTo({
				// 传递 userid 在详情页接收
				url: '/pages/tongxun/userDetail/userDetail?id=' + memberid
			});
		},
		//搜索按钮
		search(e) {
			let that = this;
			that.keyword = that.keywordinput;
			that.nextPage = 1;
			that.itemList = [];
			that.loadData(() => {

			});
		},

		//发布按钮
		release() {
			uni.navigateTo({
				url: './release/release'
			});
		},
		//打电话
		callPhone(e) {
			console.log(e)
			var phone = e.currentTarget.dataset.ph
			uni.makePhoneCall({
				phoneNumber: phone
			});
		}
	}
}
